





















































import { Component, Vue, Watch } from 'vue-property-decorator'
import { QueryFormTag } from 'src'

/**
 * @title  基本
 * @desc 最简单的用法。
 */
@Component({
  components: {
  },
  })
export default class BasicExample extends Vue {
  value = "1"

  formProps = {
    labelWidth: '120px',
    labelPosition: 'left'
  }

  form = {
    age: [],
    sex: '',
    name: ''
  }

  ages = [
    {
      label: '5-10周岁',
      value: '5,10'
    },
    {
      label: '11-20周岁',
      value: '11,20'
    },
    {
      label: '21-25周岁',
      value: '21,25'
    }
  ]

  sexes = [
    {
      label: '不限',
      value: ''
    },
    {
      label: '男',
      value: 'M'
    },
    {
      label: '女',
      value: 'F'
    }
  ]

  ageTagFn (param: any) {
    let ret: any[] = []
    let value = this.form.age || []
    value.forEach(v => {
      let target = this.ages.find(w => w.value === v)
      if (target) {
        let text = target.value
        ret.push({
          title: '年龄',
          text: target.value,
          name: 'age',
          removeFn: () => {
            let idx = (this.form.age || []).findIndex(v => v === text)
            if (idx >= 0) this.form.age.splice(idx, 1)
          }
        })
      }
    })
    return ret
  }

  sexTagFn (param: any) {
    let ret: any[] = []
    let value = this.form.sex || ''
    let target = this.sexes.find(v => v.value === value)
    if (target) {
      ret.push({
        title: '性别',
        text: target.label,
        name: 'sex',
        removeFn: () => {
          this.form.sex = ''
        }
      })
    }
    return ret
  }

  nameTagFn (param: any) {
    let ret: any[] = []
    let value = this.form.name || ''
    if (value) {
      ret.push({
        title: '姓名',
        text: value,
        name: 'name',
        removeFn: () => {
          this.form.name = ''
        }
      })
    }
    return ret
  }

  onCancel () {
    (this.$refs.form as any).cancelForm()
  }

  onSubmit (form: any) {
    (this.$refs.form as any).submitForm()
  }

  onClear () {
    (this.$refs.form as any).clearForm()
  }

  onClose (tag: QueryFormTag) {
    console.log('close : ' + tag)
  }
}
